<template>
  <!-- サイトロゴだったりバナーだったりを表示する領域 -->
  <div class="campaign-area">
    <!-- ロゴを表示 -->
    <LogoAtoms/>

    <!-- バナーはv-forで回す -->
    <ul>
      <li v-for="banner in banners" :key="banner">
        <img :src="banner" alt="banner">
      </li>
    </ul>
  </div>
</template>

<script>
import LogoAtoms from '../atoms/LogoAtoms.vue'

export default {
  name: 'CampaignArea',
  // バナーのURLを格納
  data:()=>({
    banners: [
      require('@/assets/kari-banner1.svg'),
      require('../assets/kari-banner2.svg')
    ]
  }),
  components: {
    LogoAtoms
  }
}
</script>

<style scoped>
  .campaign-area{
    width: 30vw;
    padding: 0 2vw;
    height: auto;
    background-color: rgba(100,100,100,.5);
  }

  ul{
    height: auto;
    list-style: none;
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    margin: 0;
    padding: 0;
  }

  li{
    padding: 5vh 0;
  }

  @media screen and (max-width:600px){
  .campaign-area{
    width: 100vw;
  }
}
</style>
